  display: flex;
}

li {
  list-style-type: none;
  display: inline;
}

a {
  color: #6f2dbd;
  font-family: Coming Soon, cursive;
  font-size: 2.5rem;
  text-decoration: none;
}

nav a:hover {
  color: #000;
