  text-align: center;
  background-color: #000;
  border-radius: .5rem;
  margin: .3rem;
  font-size: 2rem;
  line-height: 5rem;
}

.submenu {
  position: relative;
}

.submenu ul {
  width: 10rem;
  background-color: #000;
  list-style-type: none;
  display: none;
  position: absolute;
  top: 50px;
  left: 0;
}

.submenu:hover ul {
  display: block;
}

.submenu a:hover {
  color: #000;
  background-color: #6f2dbd;
}

h2, p {
  font-size: 3rem;
}

.home {
  width: 100%;
  height: auto;
  color: #6f2dbd;
  background-color: #000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Orbitron, sans-serif;
  display: flex;
}

.animation-container {
  width: 12rem;
  height: 12rem;
  align-items: flex-end;
  display: flex;
}

.bank, .dev {
  width: 6rem;
  height: 6rem;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.bank-item img, .dev-item img {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
}

.bank-item, .dev-item {
  opacity: 1;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
}

.bank-item {
  animation-name: out;
  animation-duration: 4s;
  animation-iteration-count: infinite;
}

.bank-item:first-child, .dev-item:first-child {
  z-index: 5;
}

.bank-item:nth-child(2), .dev-item:nth-child(2) {
  z-index: 4;
  animation-delay: 1s;
}

.bank-item:nth-child(3), .dev-item:nth-child(3) {
  z-index: 3;
  animation-delay: 2s;
}

.bank-item:nth-child(4), .dev-item:nth-child(4) {
  z-index: 2;
  animation-delay: 3s;
}

@keyframes out {
  0% {
    opacity: .2;
    transform: translateY(200%);
  }

  25% {
    opacity: 1;
    transform: translateY(150%);
  }

  50% {
    opacity: 1;
    transform: translateY(100%);
  }

  75% {
    opacity: 1;
    transform: translateY(50%);
  }

  100% {
    opacity: 0;
    transform: translate(-150%);
  }
}

.dev-item {
  animation-name: right-left;
  animation-duration: 5s;
  animation-iteration-count: infinite;
}

@keyframes right-left {
  0% {
    opacity: 1;
    transform: translate(150%);
  }

  25% {
    opacity: 1;
    transform: translateY(50%);
  }

  50% {
    opacity: 1;
    transform: translateY(150%);
  }

  75% {
    opacity: 1;
    transform: translateY(200%);
  }

  100% {
    opacity: 0;
    transform: translateY(250%);
  }
}

.home p span {
  font-family: Rubik Burned, cursive;
  font-size: 3.5rem;
}

.about, .works, .contact {
