button {
  width: 10rem;
  height: 3.5rem;
  --glow-color: #d9b0ff;
  --glow-spread-color: #bf7bff;
  --btn-color: #6f2dbd;
  border: solid .25rem var(--glow-color);
  color: var(--glow-color);
  background-color: var(--btn-color);
  box-shadow: 0 0 1rem .25rem var(--glow-color), 0 0 3rem 1rem var(--glow-spread-color), inset 0 0 .75rem .25rem var(--glow-color);
  text-shadow: 0 0 .5rem var(--glow-color);
  border-radius: 1rem;
  outline: none;
  margin-top: 3rem;
  font-size: 1.5rem;
  font-weight: bold;
  transition: all .3s;
  position: relative;
}

button:after {
  pointer-events: none;
  content: "";
  width: 100%;
  height: 100%;
  background-color: var(--glow-spread-color);
  filter: blur(2rem);
  opacity: .7;
  position: absolute;
  top: 120%;
  left: 0;
  transform: perspective(1.5rem)rotateX(35deg)scale(1, .6);
}

button:hover {
  color: var(--btn-color);
  background-color: var(--glow-color);
  box-shadow: 0 0 1rem .25rem var(--glow-color), 0 0 3rem 1rem var(--glow-color), inset 0 0 .75rem .25rem var(--glow-color);
}

button:active {
  box-shadow: 0 0 .6rem .25rem var(--glow-color), 0 0 2.5rem 2rem var(--glow-spread-color), inset 0 0 .5rem .25rem var(--glow-color);
}

.go-to {
